import React, { useState, useEffect } from 'react';
import {InfoSec, InfoRow, InfoColumn, TextWrapper, TopLine, Heading, Subtitle, ImgWrapper, Img} from './InfoSection.elements';
import {Container, Button} from '../../globalStyles'
import {Link} from 'react-router-dom'
const InfoSection = ({primary, lightBg, imgStart, lightTopLine, lightText, lightTextDesc, buttonLabel, description, headline, topLine, img, alt, start }) => {
    
    const [mmConnected, setmmConnected] = useState(false);
    useEffect(() => {
        connectWallet()
    }, []);

    window.ethereum.on('accountsChanged', (accounts) => { // listener will automatically pick up changes to accounts. We can set all switches here.
        console.log("Accounts changed triggered, new acc: ", accounts[0])

        if (accounts[0] === undefined) {
            setmmConnected(false)
        } else {
            setmmConnected(true)
        } 
      });

    const connectWallet = async () => {
        if (window.ethereum) { //check if Metamask is installed
              try {
                  const address = await window.ethereum.send('eth_requestAccounts'); //connect Metamask
                  console.log("Succesfully connected to MetaMask in infosection");
                  console.log(address)
                  setmmConnected(true)
                  const obj = {
                          connectedStatus: true,
                          status: "",
                          address: address
                      }
                      console.log("conn status: ", obj["connectedStatus"])
                      return obj;
              } catch (error) {
                  return {
                      connectedStatus: false,
                      status: " Connect to Metamask using the button on the top right."
                  }
              }
        } else {
              return {
                  connectedStatus: false,
                  status: " You must install Metamask into your browser: https://metamask.io/download.html"
              }
            } 
      };




    return (
        <>
          <InfoSec lightBg={lightBg}>
              <Container>
                  <InfoRow imgStart={imgStart}>
                      <InfoColumn>
                          <TextWrapper>
                              <TopLine lightTopLine={lightTopLine}>{topLine}</TopLine>
                              <Heading lightText={lightText}>{headline}</Heading>
                              <Subtitle lightTextDesc={lightTextDesc}>{description}</Subtitle>
                              <Link to='/sign-up'>
                                  {mmConnected ? null : <Button big fontBig onClick={connectWallet} primary={primary}>{buttonLabel}</Button>}
                              </Link>
                          </TextWrapper>
                      </InfoColumn>

                      <InfoColumn>
                          <ImgWrapper start={start}>
                              <Img src={img} alt={alt} />
                          </ImgWrapper>
                      </InfoColumn>
                  </InfoRow>
              </Container>
            </InfoSec>  
        </>
    )
}

export default InfoSection
